    },
    mounted() {

    },
    methods: {
        // 修改表情
        changeEmoji(item) {
            this.emojiShow = false;
            this.callBakValue(item)
        },
        // 关闭表情
        closeEmoji() {
            this.emojiShow = false;
            const contentDom = document.getElementById("emoji-content");
            contentDom.style.cssText = `width: 0;height: 0;`;
        },
        // 表情包开关
        hiddleEmoji() {
            if (!this.emojiShow) {
                this.callBakFun()
                this.emojiShow = true;
                const contentDom = document.getElementById("emoji-content");
                setTimeout(() => {
                    contentDom.style.cssText = `width: 608px;height: 610px;opacity:1;`;
                }, 0)
                setTimeout(() => {
                    contentDom.style.overflow = "inherit";
                }, 400)
            } else {
                this.closeEmoji()
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.emoji-wrapper {
    background: #fff;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    .emoji-box {
        width: 100%;
        height: 30px;
        display: flex;
        justify-content: flex-start;
        cursor: pointer;
        &:hover {
            color: #00a1d6;
        }
        .emoji-case {
            display: flex;
            .icon-list {
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 32px;
                margin-right: 20px;
            }
            svg {
                width: 100%;
                height: 26px;
                display: block;
            }
            .module {}
            .picture {
                svg {
                    height: 20px;
                }
            }
        }
    }
    .emoji-content {
        position: absolute;
        transition: 0.5s all;
        overflow: hidden;
        width: 0;
        height: 0;
        background: #fff;
        display: block;
        opacity: 0;
        border-radius: 6px;
        border: 1px solid #f1f1f1;
        box-shadow: 0 0 6px #f1f1f1;
        top: 40px;
        left: -15px;
        z-index: 2;
        &::after {
            content: "";
            position: absolute;
            top: -10px;
            left: 18px;
            border-style: solid;
            border-width: 0 10px 10px;
            border-color: transparent transparent #f1f1f1;
            // border-bottom: 10px solid #fff;
            // border-left: 10px solid transparent;
            // border-right: 10px solid transparent;
        }
        .span {
            display: block;
            width: 0;
            height: 0;
            border-width: 0 10px 10px;
            border-style: solid;
            border-color: transparent transparent #fff; /*透明 透明  黄*/
            position: absolute;
            top: -8px;
            left: 18px;
            z-index: 1;
        }
        .emoji-header {
            display: flex;
            width: 100%;
            justify-content: space-between;
            padding: 10px 10px 0 10px;
            box-sizing: border-box;
            .title {
                color: #999;
                padding-left: 10px;
                margin-bottom: 10px;
            }
            .cancel {
                font-size: 12px;
                cursor: pointer;
                color: #999;
                margin: 8px;
                svg {
                    width: 26px;
                    height: 26px;
                }
            }
        }
        .emoji-list {
            display: flex;
            flex-wrap: wrap;
            padding: 10px;
            width: 608px;
            box-sizing: border-box;
            .s-emoji {
                width: 32px;
                height: 32px;
                font-size: 28px;
                cursor: pointer;
                padding: 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                &:hover {
                    background: #f1f1f1;
                }
            }
        }
    }
}
</style>
